//  container sizes
$mobile: 320px;
$tablet: 768px;
$desktop: 1280px;

:root,
[data-theme='light'] {
  color-scheme: light;
  //    
  --text-accent-color: #fd5103;

  //  
  --background-accent-color: #fe7031;

  //  
  --background-primary-color: #fcfcfc;

  //    
  --background-hover-color: #f5f5f5;

  //  
  --text-primary-color: #202025;

  //   
  --text-primary-inverse-color: #ffffff;

  //  
  --text-secondary-color: #5f6775;

  //      
  --text-tertiary-color: #5f6775;

  //   
  --pagination-border-color: #eaebea;
}

:root[data-theme='dark'] {
  color-scheme: dark;
  --background-primary-color: #202025;
  --background-hover-color: #373740;
  --text-primary-color: #fcfcfc;
  --text-primary-inverse-color: #fcfcfc;
  --text-secondary-color: #fcfcfc;
  --text-tertiary-color: #bdbdbd;
  --pagination-border-color: #4f4e4e;
}
